import { createStore } from "redux";
import reducer from "./reducer.js";

export function makeStore() {
	return createStore(reducer, {
		device: true, // [true: web, false: phone]
		trigger: null, // Trigger[null,"mouseEnter","mouseLeave","click"]
		mute: true, // 
		help: false, // 
		quality: 1, //  [0.5,1]
		headsVisib: true, // Heads [true,false]
		isSliding: false, // 
		isOpen: false, // trueTails, falseHeads
		waypoint: false, //  [true,false]
		scene: false, // three  [true,false]
		sounds: {
			background: false,
			wind: false,
			whitenoise: false,
			neon: false
		}, // 
		map: { count: null, now: null },
		mapScrollTo: null, // {Number}
		setSectionChangeBegin: {},
		setSectionChangeComplete: {}
	});
}
